body {
  padding: 20px;
}

.inferrule {
  display: inline-block;
  // text-align: center;

  .divider {
    width: 100%;
    height: 2px;
    background: black;
    margin: 6px 0;
  }
}

.detail-box {
  cursor: pointer;
  display: flex;
  padding: 3px;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin: 0 auto;

  > div {
    align-self: center;
    margin: 0 auto;
  }
}

